import React from 'react';
import { withStyles } from '@material-ui/core/styles';

import GenerateBingoNumbers from './GenerateBingoNumbers.js';

const styles = theme => ({
  board: {
    height: "100%",
    width: "100%",
    border: "1px solid #999",
  },
  boardHeaderRow: {
    height: "10%",
  },
  boardRow: {
    height: "18%",
  },
  headerSquare: {
    [`@media(max-height: 450px) and (orientation: landscape)`]: {
      fontSize: "4vw",
    },
    [`${theme.breakpoints.down('xs')} and (orientation: portrait)`]: {
      fontSize: "4vw",
    },
    fontSize: "4vh",
    fontWeight: "bold",
    width: "20%",
    height: "100%",
    background: "#fff",
    border: "1px solid #999",
    padding: "0",
    textAlign: "center",
    backgroundColor:"white",  
  },
  square: {
    [`@media(max-height: 450px) and (orientation: landscape)`]: {
      fontSize: "10vw",
    },
    [`${theme.breakpoints.down('xs')} and (orientation: portrait)`]: {
      fontSize: "10vw",
    },
    fontSize: "8vh",
    fontFamily: "Arial",
    fontWeight: "bold",
    width: "20%",
    height: "100%",
    background: "#fff",
    border: "1px solid #999",
    padding: "0",
    textAlign: "center",
    backgroundColor:"white",
  },
});

class Square extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      bgColor: ""
    }
  }

  boxClick = (e) => {
    this.setState({
      bgColor: this.state.bgColor === "#FF6666" ? "white" : "#FF6666"
    })
  }

  render() {
    const { classes } = this.props;

    return (
      <button 
        className={classes.square}
        style={{backgroundColor: this.state.bgColor}}
        onClick={this.boxClick}
      >
        {this.props.value}
      </button>
    );
  }
}

class Board extends React.Component {
  constructor(props) {
    super(props);

    let bingo = new GenerateBingoNumbers(this.props.gameCode);

    this.BingoB = bingo.BingoB
    this.BingoI = bingo.BingoI
    this.BingoN = bingo.BingoN
    this.BingoG = bingo.BingoG
    this.BingoO = bingo.BingoO
  }

  renderSquare(i) {
    const StyledSquare = withStyles(styles)(Square);
    return <StyledSquare value={i} />;
  }

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.board}>
        <div className={classes.boardHeaderRow}>
          <button className={classes.headerSquare}>B</button>
          <button className={classes.headerSquare}>I</button>
          <button className={classes.headerSquare}>N</button>
          <button className={classes.headerSquare}>G</button>
          <button className={classes.headerSquare}>O</button>
        </div>
        <div className={classes.boardRow}>
          {this.renderSquare(this.BingoB[0])}
          {this.renderSquare(this.BingoI[0])}
          {this.renderSquare(this.BingoN[0])}
          {this.renderSquare(this.BingoG[0])}
          {this.renderSquare(this.BingoO[0])}
        </div>
        <div className={classes.boardRow}>
          {this.renderSquare(this.BingoB[1])}
          {this.renderSquare(this.BingoI[1])}
          {this.renderSquare(this.BingoN[1])}
          {this.renderSquare(this.BingoG[1])}
          {this.renderSquare(this.BingoO[1])}
        </div>
        <div className={classes.boardRow}>
          {this.renderSquare(this.BingoB[2])}
          {this.renderSquare(this.BingoI[2])}
          {this.renderSquare("FR")}
          {this.renderSquare(this.BingoG[2])}
          {this.renderSquare(this.BingoO[2])}
        </div>
        <div className={classes.boardRow}>
          {this.renderSquare(this.BingoB[3])}
          {this.renderSquare(this.BingoI[3])}
          {this.renderSquare(this.BingoN[3])}
          {this.renderSquare(this.BingoG[3])}
          {this.renderSquare(this.BingoO[3])}
        </div>
        <div className={classes.boardRow}>
          {this.renderSquare(this.BingoB[4])}
          {this.renderSquare(this.BingoI[4])}
          {this.renderSquare(this.BingoN[4])}
          {this.renderSquare(this.BingoG[4])}
          {this.renderSquare(this.BingoO[4])}
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(Board)
// export default Board