function shuffle(array,gameCode) {
    let currentIndex = array.length;
    let temporaryValue, randomIndex;
    while(randomIzer.length<gameCode){
        gameCode=gameCode/2;
    }
    let i = gameCode;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {

        // Pick a remaining element...
        randomIndex = Math.floor(randomIzer[i] * currentIndex);
        currentIndex -= 1;

        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
        i++;
    }

    return array;
}

export default class GenerateBingoNumbers {
    constructor(gameCode) {
        let bingoB = [];
        let bingoI = [];
        let bingoN = [];
        let bingoG = [];
        let bingoO = [];

        let numberTotal = 75;
        let gridWidth = 5;

        let tempArray = [bingoB,bingoI,bingoN,bingoG,bingoO];
        let count = 1;
        for(let i = 0; i < gridWidth; i++) {
            for (let j = 0; j < Math.round(numberTotal / gridWidth); j++) {
                tempArray[i].push(count);
                count++;
            }
        }

        shuffle(bingoB,gameCode); shuffle(bingoI,gameCode); shuffle(bingoN,gameCode); shuffle(bingoG,gameCode); shuffle(bingoO,gameCode);
    
        this.BingoB = bingoB;
        this.BingoI = bingoI;
        this.BingoN = bingoN;
        this.BingoG = bingoG;
        this.BingoO = bingoO;
    }
}